.app__aboutus{
    position: relative;
}

.app__aboutus-overlay{
 position: absolute;
 inset: 0;    
}

.app__aboutus-overlay img {
    width: 391px;
    height: 415px;
    z-index: 0;
}

.app__aboutus-content {
    width: 100%;
    z-index: 2;
}

.app__aboutus-content_about {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    
    text-align: right;
}

.app__aboutus-content_knife{
    margin: 2rem 4rem;    
}


.app__aboutus-content_knife img{
    height: 910px;
    animation: fadeIn linear;
    animation-timeline: view();
    animation-range: entry 0 cover 70%;
}

@keyframes appear {
    from {
        opacity: 0%;
        scale: 0;
    }
    to {
        opacity: 1;
        scale: 1;
    }
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}


.app__aboutus-content_history {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    
    text-align: left;
}

.app__aboutus-content_about p ,.app__aboutus-content_history p {
    margin: 2rem 0;
    color: var(--color-grey);
} 

@media screen and (max-width: 2000px) {
    .app__aboutus-content_knife img{
        height: 1110px;
    }

    .app__aboutus-content_about p ,.app__aboutus-content_history p {
        margin: 4rem 0;
        color: var(--color-grey);
    } 
}

@media screen and (max-width: 900px) {
    .app__aboutus-content {
        flex-direction: column;
    }
    .app__aboutus-content_knife{
        margin: 4rem 0;
    }
}
    

@media screen and (max-width: 650px) {
    .app__aboutus-overlay img {
        width: 80%;
        height: 320px;
    }
}
    